@import '../src/mixins/_all'

$flex-direction-values: row, row-reverse, column, column-reverse
@each $value in $flex-direction-values
  .is-flex-direction-#{$value}
    flex-direction: $value

$flex-wrap-values: nowrap, wrap, wrap-reverse
@each $value in $flex-wrap-values
  .is-flex-wrap-#{$value}
    flex-wrap: $value

$justify-content-values: flex-start, flex-end, center, space-between, space-around, space-evenly, start, end, left, right
@each $value in $justify-content-values
  .is-justify-content-#{$value}
    justify-content: $value

$align-content-values: flex-start, flex-end, center, space-between, space-around, space-evenly, stretch, start, end, baseline
@each $value in $align-content-values
  .is-align-content-#{$value}
    align-content: $value

$align-items-values: stretch, flex-start, flex-end, center, baseline, start, end, self-start, self-end
@each $value in $align-items-values
  .is-align-items-#{$value}
    align-items: $value

$align-self-values: auto, flex-start, flex-end, center, baseline, stretch
@each $value in $align-self-values
  .is-align-self-#{$value}
    align-self: $value

$flex-operators: grow, shrink
@each $operator in $flex-operators
  @for $i from 0 through 5
    .is-flex-#{$operator}-#{$i}
      flex-#{$operator}: $i

@each $name, $value in $bal-spacing-values
  .fg-#{$name}
    gap: $value

@include tablet()
  @each $name, $value in $bal-spacing-values-tablet
    .fg-#{$name}
      gap: $value

@include desktop()
  @each $name, $value in $bal-spacing-values-desktop
    .fg-#{$name}
      gap: $value

@if $spacing-legacy-numbering
  @each $name, $value in $bal-legacy-spacing-values
    .fg-#{$name}
      gap: $value

  @include tablet()
    @each $name, $value in $bal-legacy-spacing-values-tablet
      .fg-#{$name}
        gap: $value

  @include desktop()
    @each $name, $value in $bal-legacy-spacing-values-desktop
      .fg-#{$name}
        gap: $value
